.Container{
    padding: 0;
    border: 0;
    font-size: 100%;
    vertical-align: baseline;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;

}
.btn{
    border-style: solid;
    border-width: 1px;
    border-color: rgb(174, 134, 37);
    font-size: 14px;
    font-family: 'Work Sans';
    font-weight: 600;
    color: #dbdbdb;
    width: 100%;
    line-height: inherit;
    text-align: center;
    border-radius: 6px;
    background-color: #000;
    transition: 0.4s;
    padding: 5px 9px;
    
}
.btn:hover{
    background-image: -webkit-linear-gradient( 12deg, rgb(174,134,37) 0%, rgb(247,239,138) 42%, rgb(210,172,71) 79%, rgb(237,201,103) 100%);
    color: #000 !important;
    border-style: solid;
    border-width: 1px;
    border-color: rgb(174, 134, 37);
    font-size: 14px;
    transition: all 0.5s ease-out;
    text-transform: uppercase;
    display: inline-block;
}
.currentBtn{
    background-image: -webkit-linear-gradient( 12deg, rgb(174,134,37) 0%, rgb(247,239,138) 42%, rgb(210,172,71) 79%, rgb(237,201,103) 100%);
    color: #000 !important;
    border-style: solid;
    border-width: 1px;
    border-color: rgb(174, 134, 37);
    font-size: 14px;
    font-family: 'Work Sans';
    font-weight: 600;
    color: #dbdbdb;
    width: 100%;
    line-height: inherit;
    text-align: center;
    border-radius: 6px;
    background-color: #000;
    transition: 0.4s;
    padding: 5px 9px;
    transition: all 0.5s ease-out;
    text-transform: uppercase;
    display: inline-block;
}




@media only screen and (max-width: 700px) {
    .Container {
        flex-wrap: wrap;
        justify-content: start;
        
    }
    .Container .btn{
        width:130px;
        font-size: 13px;
    }
    
}